@content4: content4;

.@{content4}-wrapper {
  margin-top: 0px;
  background-color: #000;
  
  .@{content4} {
    padding: 0 24px;
	
    &-text {
	  height: auto;
	  text-align: center;
	  margin-top: 10px;
      padding: 0 32px;
      .@{content4}-content {
	  	color: #fff;
	    width: 100%;
		margin-top: 40px;
		text-align: justify;
		line-height: 24px;
      }
      .@{content4}-title {
	    color: #d89c31;
        font-size: 32px;
        margin: 32px auto 16px;
	    line-height: 32px;
        font-size: 32px;
      }
      .content {
        margin-top: 20px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .@{content4}-wrapper {
    .@{content4} {
      &-text {
        height: 750px;
        margin-bottom: 0px;
        text-align: center;
        padding: 0;
        .@{content4}-content,
        .@{content4}-title {
          width: 100%;
          top: auto;
        }
        .@{content4}-title {
          margin: 32px auto 16px;
          font-size: 32px;
        }
      }
    }
  }
}
