  color: #fff;
  padding:  25px;
  padding-bottom: 90px;
  
  .faq-body {
    .faq-row {
	  border-top: 1px solid #d89c31;
	  border-bottom: 0;
	  line-height: 1.75em;
	  
      .row-content {
