@content2: content2;

.@{content2}-wrapper {
  min-height: 446px;
  overflow: hidden;
  background-color: #000;

  .@{content2} {
    height: 100%;
    padding-top: 64px;

    >.title-wrapper {
      margin: 0;
	  >h1 {
	    color: #d89c31;
	  }
    }

    &-block {
      padding: 0;
      display: inline-block;
      text-align: center;
      min-height: 200px;
      img {
        width: 100%;
      }

      &-wrapper {
        position: relative;
        height: 100%;
        top: 25%;
        padding: 10px;
      }

      &.queue-anim-leaving {
        position: relative !important;
      }

      &-icon {
        width: 140px;
        height: 140px;
        margin: auto;
      }

      &-title {
	    color: #fff;
        line-height: 32px;
        margin: 0px auto;
	    margin-top: 0px;
		font-size: 24px;
      }
    }
	
    &-content {
	  margin-top: 30px;
	  color: #fff;
	  text-align: justify;
    }
  }
}

@media screen and (max-width: 767px) {
  .@{content2}-wrapper {
    min-height: 700px;
  }
}
