@banner0: banner0;
.@{banner0} {
  height: 100vh;
  position: relative;
  border-color: #666;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-image: url("https://drive.google.com/uc?id=1n-JELnphe20hiKgOJBhxR9loZEeZxRkP");
  text-align: right;
	
  & &-text-wrapper {
    display: inline-block;
    position: absolute;
    margin: auto;
	margin-top: -20px;
    right: 0;
    font-size: 14px;
    color: @template-text-color-light;
    width: 100%;
    >.queue-anim-leaving {
      position: relative !important;
    }
  }
  & &-title {
    width: 100%;
    right: 10%;
    min-height: 60px;
    margin: auto;
    display: inline-block;
    font-size: 40px;
    position: relative;
  }
  & &-title-mobile {
    width: 100%;
    right: 10%;
    min-height: 60px;
	margin-top: -20%;
	padding-bottom: 15%;
    display: inline-block;
    font-size: 40px;
    position: relative;
  }
  & &-content {
    margin-bottom: 20px;
    word-wrap: break-word;
    min-height: 24px;
  }
  & &-button {
    border: 2px solid #d89c31;
    color: #d89c31;
    background: transparent;
    box-shadow: 0 0 0 transparent;
    font-size: 14px;
    height: 40px;
	width: 80px;
    transition: background .45s @ease-out, box-shadow .45s @ease-out;
	border-radius: 5px;
	right: 5%;
	top: 10%;
    &:hover {
      color: #d89c31;
      border-color: #d89c31;
      background: rgba(255, 255, 255, 0.1);
      box-shadow: 0 0 10px rgba(250, 250, 255, 0.75);
    }
    &:focus {
      color: #d89c31;
      border-color: #d89c31;
    }
    &.queue-anim-leaving {
      width: auto;
    }
  }
  & &-icon {
    bottom: 20px;
    font-size: 24px;
    position: absolute;
    left: 50%;
    margin-left: -12px;
    color: @template-text-color-light;
  }
  
  .flip-countdown {
    margin-top: 35px;
	color: #d89c31;
  }
}


.icons {
    height: 40px;
	width: 80px;
	left: 5%;
	top: 10%;
	float: left;
	
  .icon-twitter {
    margin-top: 5%;
  }
  .icon-discord {
    margin-top: 5%;
	margin-left: 15%;
  }
}


@media screen and (max-width: 767px) {
  .@{banner0} {
    background-attachment: inherit;
	height: 95vh;
  }
}
